function generateInputRange(start, end, numPoints, range) {
	const inputRange = document.getElementById(range)

	if (inputRange) {
		const step = (end - start) / (numPoints - 1)

		inputRange.setAttribute('min', start)
		inputRange.setAttribute('max', end)
		inputRange.setAttribute('step', step)
		inputRange.setAttribute('value', start) // Добавляем начальное значение

		return inputRange
	}
}

const startValue = 0 // Начальное значение
const endValue = 10 // Конечное значение
const numPoints = 3 // Количество опорных точек

const delay = generateInputRange(startValue, endValue, numPoints, 'rangeDelay')
const intensity = generateInputRange(
	startValue,
	endValue,
	numPoints,
	'rangeIntensity'
)
